import { Component, Vue } from 'vue-property-decorator';

@Component
export default class A1Template extends Vue {
  absolute: any = false;
  alpha: any = 0;
  beta: any = 0;
  gamma: any = 0;

  get Style() {
    return `height 40px; width:5px; background white; transform:rotate(${this.alpha}deg)`;
  }

  handleOrientationEvent (event: any) {
    // do something amazing
    this.absolute = event.absolute;
    this.alpha = event.alpha;
    this.beta = event.beta;
    this.gamma = event.beta;
  };

  mounted() {
    window.addEventListener('deviceorientationabsolute', event => this.handleOrientationEvent(event));

    this.$root.$emit('set-info', {
      title: 'App Title',
      info: `App description and instructions.`,
    });
  }
}
